.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-image: url('../assets/purple-icon.png'); 
    background-size: cover;
    background-position: center;
    height: 80px;
    padding: 10px 20px;
    position: relative; 
    z-index: 0; 
  }
  
  .navbar::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); 
    z-index: -1; 
  }
  
  .logo {
    color: white;
    font-size: 1.8rem;
    font-weight: bold;
    z-index: 1; 
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.8); 
  }

  .menu a {
    color: white;
    margin-left: 20px;
    text-decoration: none;
    z-index: 1; 
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.8); 
    padding: 10px 15px; 
    background-color: rgba(0, 0, 0, 0.3); 
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .menu a:hover {
    background-color: rgba(0, 0, 0, 0.6); 
    color: #ffffff;
  }
  

  .logout-button {
    background-color: rgba(0, 0, 0, 0.3); 
    border: none;
    padding: 10px 15px;
    margin-left: 20px;
    cursor: pointer;
    z-index: 1; 
    color: white;
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: rgba(0, 0, 0, 0.6)
  }
  